<template>
	<div>
		<h3>List Customer</h3>
		<table style="width: 100%;">
			<thead>
				<tr>
					<th>ID</th>
					<th>Nama</th>
					<th>Usia</th>
					<th>Tindakan</th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="c in customers" :key="c">
					<td>{{ c.customer_id }}</td>
					<td>{{ c.nama }}</td>
					<td>{{ c.usia }}</td>
					<td>
						<button @click="getCustomerById(c.customer_id)">cek</button>
					</td>
				</tr>
			</tbody>
		</table>
		<div v-if="detailCustomer">
			<ul>
				<li><strong>Customer ID:</strong> {{ detailCustomer.customer_id }}</li>
				<li><strong>Nama:</strong> {{ detailCustomer.nama }}</li>
				<li><strong>Jenis Kelamin:</strong> {{ detailCustomer.jen_kel }}</li>
				<li><strong>Usia:</strong> {{ detailCustomer.usia }}</li>
				<li><strong>Alamat:</strong> {{ detailCustomer.alamat }}</li>
			</ul>
		</div>
	</div>
</template>

<script>
import axios from 'axios';
export default {
	name: 'CustomerView',
	data() {
		return {
			keyword: '',
			customers: [],
			detailCustomer: []
		}
	},
	created() {
		this.getAllCustomers();
	},
	methods: {
		async getAllCustomers() {
			try {
				const response = await axios.get('/customers');
				if (response) {
					this.customers = response.data;
				}
			} catch (err) {
				console.log(err);
			}
		},
		async getCustomerById(id) {
			try {
				const response = await axios.get('/customer/' + id);
				if (response) {
					this.detailCustomer = response.data;
				}
			} catch (err) {
				console.log(err);
			}
		}
	}
}
</script>

